
// import $ from '../lib/jquery-3.3.1.min';
// import samples from './samples';
import ajax from './ajax';

export default class Api {
    constructor(options) {
        this.options = options;
    }

    init() {
        if (this._init === undefined) {
            this._init = $.Deferred().resolve();
        }
        return this._init;
    }

    getConfig() {
        const { host, view, accessKey } = this.options;
        return ajax.get(`${host}/api/v1/views/configuration/${view}`, { 'X-VIEW-KEY': `${view}:${accessKey}` });
    }

    getEntries(url) {
        const {
            host,
            view,
            accessKey,
            clientId
        } = this.options;

        if (url) {
            const fullUrl = `${host}/${url}`;
            return ajax.get(fullUrl, { 'X-VIEW-KEY': `${view}:${accessKey}` });
        }
        else {
            const defaultUrl = `${host}/api/v1/views/${view}/${encodeURIComponent(clientId)}`;
            return ajax.get(defaultUrl, { 'X-VIEW-KEY': `${view}:${accessKey}` });
        }
    }

    reportError(file, functionName, displayedMessage, error) {
        this.lastError = error;
        // console.log(JSON.stringify(error));
        const { host, view, accessKey } = this.options;
        return ajax.post(`${host}/api/v1/errors`, { 'X-VIEW-KEY': `${view}:${accessKey}` }, {
            file,
            function: functionName,
            displayedMessage,
            error: JSON.stringify(error)
        });
    }
}
